import PropTypes from 'prop-types';
import React from 'react';
import Gallery from 'react-grid-gallery';
import LinearProgress from '@material-ui/core/LinearProgress';
import ShareIcon from '@material-ui/icons/Share';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import LinkIcon from '@material-ui/icons/Link';
import TextField from '@material-ui/core/TextField';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';

const styles = theme => ({
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    dialogText: {
        color: theme.palette.blue
    }
});

class imageList extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            images: [],
            currentImage: 0,
            currentImagePath: "",

            error: null,
            isLoaded: false,

            open: false,
            //for modal
            uuid: "not_found",
            mode: "nil",
            permission: "anyone",
            ShareError: null,
        };

        this.onCurrentImageChange = this.onCurrentImageChange.bind(this);
        this.shareImage = this.shareImage.bind(this);
        this.handleDialogClose = this.handleDialogClose.bind(this);
        //this.onChangeValue = this.onChangeValue.bind(this);
    }

    onCurrentImageChange(index) {
        this.setState({ currentImage: index });
    }

    handleDialogClose() {
        this.setState({
            open: false
        });
    }

    shareImage = () => {
        var CIP = this.state.images[this.state.currentImage].vsrc;
        this.setState({ open: true, currentImagePath: CIP });
        fetch("/system/file_system/share/new?path=" + CIP, {})
        //fetch("/backend_test/new?path=" + CIP, {})
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        uuid: result.UUID,
                        mode: result.Permission,
                    });
                    console.log(this.state.uuid);
                },
                // Note: it's important to handle errors here
                // instead of a catch() block so that we don't swallow
                // exceptions from actual bugs in components.
                (error) => {
                    this.setState({
                        ShareError: error
                    });
                }
            )
    }
    
    //modal
    /*
    onChangeValue(event) {
        console.log(event.target.value);
    }
*/
    handleChange = (event) => {
        this.setState({ mode: event.target.value });

        var uuid = this.state.uuid;
        var mode = event.target.value;
        var path = this.state.currentImagePath;

        if (event.target.value != "remove") {
            //this.setState({ currency: event.target.value });
            //fetch('/backend_test/edit?uuid=' + uuid + '&mode=' + mode, {
            fetch('/system/file_system/share/edit?uuid=' + uuid + '&mode=' + mode, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json'
                }
            })
                .then((response) => response.json())
                .then((data) => data);
        } else {
            //remove but use edit response now
            //fetch("/backend_test/remove?path=" + path, {
            fetch('/system/file_system/share/delete?path=' + path, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json'
                }
            })
                .then((response) => response.json())
                .then((data) => data);
        }
    }
    
    componentDidMount() {
       // fetch("/backend_test/listFile", {})
        
        fetch("/system/ajgi/interface?script=/Photo/backend/listFile.js", {
                method: 'post',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ folder: this.props.path })
            })
            
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        images: result
                    });
                },
                // Note: it's important to handle errors here
                // instead of a catch() block so that we don't swallow
                // exceptions from actual bugs in components.
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error: error
                    });
                }
            )
    }

    setPath() {
        this.setState({ path: "user:/Photo/Photo/uploads/" });
    }

    //handle modal

    render() {
        const { classes } = this.props;
        const { isLoaded, error } = this.state;
        const theme = {
            margin: 4,
        }
        
        if (error) {
            return <div>Error: {error.message} (imageList.js)</div>;
        }else if(!isLoaded) {
            return <div><LinearProgress /></div>;
        } else {
            return ( 
                <div>
                <Gallery images = { this.state.images }
                enableLightbox = { true }
                enableImageSelection={false}
                currentImageWillChange={this.onCurrentImageChange}

                customControls={[
                    <Button
                        variant="contained"
                        size="large"
                        style={theme}
                        startIcon={<ShareIcon />}
                        onClick={this.shareImage.bind(this)}
                    >Share</Button>
                ]}/>

                    <Dialog
                        style={{ zIndex: 99999 }}
                        open={this.state.open}
                        maxWidth="md"
                        fullWidth="true"
                        onClose={this.handleDialogClose}
                    >
                        <DialogTitle>
                                   <List>
                                    <ListItem>
                                        <ListItemAvatar>
                                            <Avatar>
                                                <LinkIcon />
                                            </Avatar>
                                        </ListItemAvatar>
                                        Get link
                                    </ListItem>
                            </List>
                        </DialogTitle>
                        <DialogContent>
                            <TextField
                                label="Shared link"
                                defaultValue="https://arozos.com/share/not_found"
                                InputProps={{
                                    readOnly: true,
                                }}
                                variant="filled"
                                fullWidth="true"
                                value={window.location.origin + "/share?id=" + this.state.uuid}
                            />
                            <br/><br/>
                            <TextField
                                select
                                label="Share option"
                                value={this.state.mode}
                                onChange={this.handleChange}
                                SelectProps={{
                                    native: true,
                                }}
                                variant="outlined"
                                fullWidth="true"
                            >
                                {[
                                    {
                                        label: 'Anyone with the link',
                                        value: 'anyone',
                                    },
                                    {
                                        label: 'Anyone signed in',
                                        value: 'signedin',
                                    },
                                    {
                                        label: 'Users in the same group',
                                        value: 'samegroup',
                                    },
                                    {
                                        label: 'Not public',
                                        value: 'remove',
                                    },
                                ].map((option) => (
                                    <option key={option.value} value={option.value}>
                                        {option.label}
                                    </option>
                                ))}
                            </TextField>
                            <br /><br/>
                            <Button variant="contained" color="primary" onClick={this.handleDialogClose}>
                                Done
</Button>
                        </DialogContent>
                    </Dialog> 
                </div >
            );
        }
    }
}

imageList.propTypes = {
    images: PropTypes.arrayOf(
        PropTypes.shape({
            src: PropTypes.string.isRequired,
            vsrc: PropTypes.string.isRequired,
            thumbnail: PropTypes.string.isRequired,
            srcset: PropTypes.array,
            caption: PropTypes.oneOfType([
                PropTypes.string,
                PropTypes.element
            ]),
            thumbnailWidth: PropTypes.number.isRequired,
            thumbnailHeight: PropTypes.number.isRequired
        })
    ).isRequired
};


imageList.defaultProps = {
    images: []
};

export default imageList;